.gallery-section {
    padding-bottom: 80px;
    background: rgb(255, 245, 246);
    background: linear-gradient(180deg,
            rgba(255, 245, 246, 1) 30%,
            rgba(255, 255, 255, 1) 30%);

    background: rgb(255, 245, 246);
    background: linear-gradient(180deg, rgba(255, 245, 246, 1) 30%, rgba(255, 255, 255, 1) 30%);

    @media screen and (min-width: $tablet) {
        padding-bottom: 60px;
    }

    @media screen and (min-width: $comp) {
        padding-bottom: 120px;
    }
}

.gallery {
    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__list-one,
    &__list-two,
    &__list-three,
    &__list-four,
    &__list-five {
        margin-right: 2px;

        @media screen and (min-width: 768px) {
            margin-right: 4px;
        }

        @media screen and (min-width: 1200px) {
            margin-right: 7px;
        }
    }

    &__list-four {
        margin-top: 10px;

        @media screen and (min-width: 768px) {
            margin-top: 20px;
        }

        @media screen and (min-width: 1200px) {
            margin-top: 30px;
        }
    }

    &__list-five {
        margin-bottom: 15px;

        @media screen and (min-width: 768px) {
            margin-bottom: 45px;
        }

        @media screen and (min-width: 1200px) {
            margin-bottom: 55px;
        }
    }

    &__image-five {
        max-width: 40px;

        @media screen and (min-width: 768px) {
            max-width: 105px;
        }

        @media screen and (min-width: 1200px) {
            max-width: 163px;
        }
    }

    &__image-one,
    &__image-two,
    &__image-three,
    &__image-four,
    &__image-five,
    &__image-six,
    &__image-seven,
    &__image-eight,
    &__image-nine,
    &__image-ten {
        width: 100%;
        overflow: hidden;
        border-radius: 8px;
        transform: scale(1) translateX(0);
        transition: transform 250ms ease-in-out;

        @media screen and (min-width: 320px) {
            border-radius: 10px;
        }

        @media screen and (min-width: 768px) {
            border-radius: 16px;
        }

        @media screen and (min-width: 1200px) {
            border-radius: 22px;
        }
    }

    &__image-one:hover,
    &__image-one:focus {
        cursor: pointer;
        transform: scale(1.3) translateX(-8px);

        @media screen and (min-width: 320px) {
            transform: scale(1.3) translateX(-12px);
        }

        @media screen and (min-width: 768px) {
            transform: scale(1.3) translate(-15px);
        }
    }

    &__image-two:hover,
    &__image-two:focus {
        cursor: pointer;
        transform: scale(1.3) translate(-8px, -8px);

        @media screen and (min-width: 768px) {
            transform: scale(1.3) translate(-10px, -10px);
        }

        @media screen and (min-width: 1200px) {
            transform: scale(1.3) translate(-18px, -18px);
        }
    }

    &__image-three:hover,
    &__image-three:focus {
        cursor: pointer;
        transform: scale(1.3) translate(-8px, 8px);

        @media screen and (min-width: 768px) {
            transform: scale(1.3) translate(-10px, 10px);
        }

        @media screen and (min-width: 1200px) {
            transform: scale(1.3) translate(-20px, -20px);
        }
    }

    &__image-four:hover,
    &__image-four:focus {
        cursor: pointer;
        z-index: 1;
        transform: scale(1.3) translate(-15px, -15px);

        @media screen and (min-width: 768px) {
            transform: scale(1.3) translate(-22px, -22px);
        }

        @media screen and (min-width: 1200px) {
            transform: scale(1.3) translate(-37px, -37px);
        }
    }

    &__image-five:hover,
    &__image-five:focus {
        cursor: pointer;
        transform: scale(1.3) translateX(10px);

        @media screen and (min-width: 768px) {
            transform: scale(1.3) translateX(17px);
        }

        @media screen and (min-width: 1200px) {
            transform: scale(1.3) translateX(20px);
        }
    }

    &__image-six:hover,
    &__image-six:focus {
        cursor: pointer;
        transform: scale(1.3) translate(-8px, -8px);

        @media screen and (min-width: 768px) {
            transform: scale(1.3) translate(-10px, -10px);
        }

        @media screen and (min-width: 1200px) {
            transform: scale(1.3) translate(-18px, -18px);
        }
    }

    &__image-seven:hover,
    &__image-seven:focus {
        cursor: pointer;
        transform: scale(1.3) translateX(-8px);

        @media screen and (min-width: 768px) {
            transform: scale(1.3) translateX(-10px);
        }

        @media screen and (min-width: 1200px) {
            transform: scale(1.3) translateX(-15px);
        }
    }

    &__image-eight:hover,
    &__image-eight:focus {
        cursor: pointer;
        transform: scale(1.3) translate(-8px, -8px);

        @media screen and (min-width: 768px) {
            transform: scale(1.3) translate(-10px, -10px);
        }

        @media screen and (min-width: 1200px) {
            transform: scale(1.3) translate(-18px, -18px);
        }
    }

    &__image-nine:hover,
    &__image-nine:focus {
        cursor: pointer;
        transform: scale(1.3) translate(-8px, 8px);

        @media screen and (min-width: 768px) {
            transform: scale(1.3) translate(-10px, 10px);
        }

        @media screen and (min-width: 1200px) {
            transform: scale(1.3) translate(-15px, 15px);
        }
    }

    &__image-ten:hover,
    &__image-ten:focus {
        cursor: pointer;
        transform: scale(1.3) translate(8px);

        @media screen and (min-width: 768px) {
            transform: scale(1.3) translate(12px);
        }

        @media screen and (min-width: 1200px) {
            transform: scale(1.3) translate(15px);
        }
    }

    &__image-three,
    &__image-five,
    &__image-seven,
    &__image-nine {
        margin-top: 2px;

        @media screen and (min-width: 768px) {
            margin-right: 4px;
        }

        @media screen and (min-width: 1200px) {
            margin-right: 7px;
        }
    }
}